import React from "react";
import FileDownloader from "./tweet_vid_download"
const axios = require("axios").default;

class TweetVid extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        id: null,
        url: null
    };
    this.searchVideoFromTweet = this.searchVideoFromTweet.bind(this);
  }
  searchVideoFromTweet(pathname){
    axios.get("/tweet/"+pathname+".mp4", {})
    .then(response => {
      if(!response.data.err){
      if (response.data) {
        this.setState({
          url: response.data.data,
        });
        console.log(response);
        this.setState({redirect: true});
      }
    }
    })
    .catch(error => {
      console.log("login error", error);
    });
  }
  componentDidMount() {
    let pathname = this.props.location.pathname.substring(12);
    let url = this.searchVideoFromTweet(pathname);
    this.setState({id: pathname});
    this.setState({url: url});
  }
  render() {
    const { id, url } = this.state
    return (
      <div id="mainTweetVidContainer">
        {id &&
          <div id="tweetVidContainer" className="col-sm-12">
            <video width="700" height="400" controls>
              <source src={`/downloaded/`+id+`.mp4`} type="video/mp4"/>{/*1455127684498051073 // window.location.protocol + `//` + window.location.hostname +*/}
            </video>
            <FileDownloader file={url} fileName={id+`.mp4`} urlTweet={this.props.history.location.state ? this.props.history.location.state.urlTweet : undefined}/>
          </div>
        }
      </div>
    )
  }
}

export default TweetVid;