import React from "react";
import "./tweet_vid.css";
import Button from 'react-bootstrap/Button';
import useDownloader from "react-use-downloader";

const FileDownloader = ({file,fileName,urlTweet}) => {
  const {
    download,
    error
  } = useDownloader();
  return (
      <div className="row">
        <div className="col text-center">
          <div className="btn-event">
            <Button variant="secondary" onClick={() => download(file, fileName)}>Download</Button>
            {urlTweet && <Button variant="secondary" onClick={() => window.open(urlTweet, "_blank")}>View original tweet</Button>}
            <Button variant="secondary" onClick={() => window.open("https://twitter.com/cocodlbot/", "_blank")}>Follow coco_bot</Button>
          </div>
        </div>
        {/* <p>Download size in bytes {size}</p>
        <label>Downloading progress:</label>
        <progress id="file" value={percentage} max="100" />
        <p>Elapsed time in seconds {elapsed}</p> */}
        {error && <p>possible error {JSON.stringify(error)}</p>}
      </div>
  );
};

export default FileDownloader;