import React from "react";
import "./mainContainer.css";
import TweetVid from "./tweet_vid";
import { Link } from 'react-router-dom';
const axios = require("axios").default;

class MainContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        objUrl: []
    };
    this.convertDate = this.convertDate.bind(this);
  }
convertDate(inputFormat) {
    function pad(s) { return (s < 10) ? '0' + s : s; };
    var d = new Date(inputFormat);
    return [pad(d.getDate()), pad(d.getMonth()+1), d.getFullYear()].join('/');
  }
  componentDidMount() {
    axios.get("/tweet/", {})
    .then(response => {
      if(!response.data.err){
      if (response.data) {
        this.setState({
          objUrl: response.data.data,
        });
      }
    }
    })
    .catch(error => {
      console.log("login error", error);
    });
  };
   componentDidUpdate(prevProps, prevState){
    if (prevState.objUrl !== this.state.objUrl) {
       return;
    }
  }
  render() {
    const {objUrl} = this.state;
    const {convertDate} = this;
    return (
      <div id="mainContainer" className="text-center col-sm-12">
        <div><h3>Welcome to Cocodlbot</h3></div>
        <br/>
        <div id="mainContainer" className="main-container-pos col-sm-12">
          {this.props.location && <TweetVid/>}
          {objUrl.length !== 0 && objUrl.map(function(item, index) {
            <div>Liste des derniers téléchargements</div>
            return(
              <Link to={{pathname: item.url_tweet.includes(".mp4") ? "/home/tweet/"+item.url_tweet.substring(0,Object.keys(item.url_tweet)[0].indexOf(".mp4")) : item.url_tweet, state: { url: item.expanded_url, urlTweet : item.id_tweet}}} className="disp-card">
                  <div id={"tweetVidContainer"+index} className="col-sm-12" key={index} data-key={index}>
                  <div id={"tweetVid"+index} >
                  <div className="card">
                    <div className="card_image"> <img src={`/downloaded/`+item.thumbnail} alt="Avatar" /> </div> {/*window.location.protocol + `//` + window.location.hostname +*/}
                    <div className="card_title title-white">
                        <h5>Mise en ligne par <b>{item.user_info.substring(item.user_info.lastIndexOf('%')+1).trim()}</b></h5> 
                        <h5>Le <b>{convertDate(item.user_info.substring(0,item.user_info.split("").findIndex((element) => element === "%")).trim())}</b></h5>
                        {/* <h5>Téléchargé par <b>{item[3].substring(item[3].lastIndexOf('%')+1).trim()}</b></h5>
                        <h5>Le {convertDate(item[3].substring(0,item[3].split("").findIndex((element) => element === "%")).trim())}</h5> */}
                    </div>
                  </div>
                  </div>
                </div>
              </Link>
            )
          })}
        </div>
      </div>
    )
  }
}

export default MainContainer;