import React, { Component } from 'react'
import './App.css';
import Cartouche from './cartouche';
import MainContainer from './mainContainer';
import ErrorContainer from './errorContainer';
import "./home.css";

class home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataTweet: []
    };
    this.handleResultSearch = this.handleResultSearch.bind(this);
  }

  handleResultSearch(data) {
    this.setState({ dataTweet: data});
  }

  render() {
    return (
      <div className="wrapper" id="container">
          <Cartouche/>
          <ErrorContainer/>
          <div id="searchContainer">
            <div id="navRight" className="col-sm-3"></div>
            <div id="navLeft" className="col-sm-3"></div>
          </div>
          <MainContainer resultSearch={this.state.dataTweet}/>
          <div id="modalContainer"></div>
      </div>
    )
  }
}

export default home;
